function selectOption() {
  $('.select').select2({
    width: '100%',
    minimumResultsForSearch: Infinity,
		placeholder: function() {
			$(this).data('placeholder')
		}
  })
}

if (document.querySelector('.select')) {
  $(document).ready(function() {
    selectOption()
  })
}



