document.addEventListener("DOMContentLoaded", function(event) {
  
  let examplesSlider = new Swiper('.examples-slider', {
    spaceBetween: 30,
    loop: true,
    slidesPerView: 2,
  
    navigation: {
      nextEl: '.swiper-next',
      prevEl: '.swiper-prev',
    },

    breakpoints: {
      576: {
        slidesPerView: 1,
      },
    },
    autoplay: {
      delay: 5000,
      disableOnInteraction: false,
    },
  })
  
  let timelineSlider = new Swiper('.timeline-slider', {
    loop: true,
    slidesPerView: 1,
  
    navigation: {
      nextEl: '.timelineSlider-next',
      prevEl: '.timelineSlider-prev',
    },
    
  })
})

export function albumSlider() {
  let albumSliderThumbs = new Swiper('.album-slider-thumbs', {
    spaceBetween: 30,
    slidesPerView: 3,
    watchSlidesVisibility: true,
    // breakpoints: {
    //   1200: {
    //     slidesPerView: 4
    //   },
    //   768: {
    //     slidesPerView: 3
    //   }
    // }
  })

  let albumSliderTop = new Swiper('.album-slider-top', {
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
    thumbs: {
      swiper: albumSliderThumbs
    }
  })
}

// ----------------------------------------------

let galleryThumbs = new Swiper(".slider-2", {
  spaceBetween: 45,
  simulateTouch: false,
  slidesPerView: 3,
  loop: true,
  watchSlidesVisibility: true,
  watchSlidesProgress: true,
  breakpoints: {
    768: {
      slidesPerView: 2
    }
  },
  autoplay: {
    delay: 5000,
    disableOnInteraction: false,
  },
});

let galleryTop = new Swiper('.slider-1', {
  loop: true,
  simulateTouch: false,
  navigation: {
    nextEl: '.swiper-next',
    prevEl: '.swiper-prev',
  },
  thumbs: {
    swiper: galleryThumbs
  },
  autoplay: {
    delay: 5000,
    disableOnInteraction: false,
  },
});



