function animatedBlock() {
  
  let animation_elements = [...document.querySelectorAll('.js-animate')]

  function check_if_in_view() {
  
    let windowHeight = innerHeight / 2
     
    animation_elements.forEach(function(el) {
  
      let itemHeight = el.getBoundingClientRect().top
      
      if (itemHeight < windowHeight) {
        let animateType = el.dataset.typeAnimate
        
        el.classList.add('animated')
        el.classList.add(animateType)

        if (el.querySelectorAll('.js-animate-card')){
          let cards = [...el.querySelectorAll('.js-animate-card')]
          cards.forEach(elem => {
            let cardTypeAnimate = elem.dataset.typeAnimate
            elem.classList.add('animated', `${cardTypeAnimate}`)
            elem.classList.add(cardTypeAnimate)
          })
        }
      } else {
        // el.classList.remove('animated', 'fadeIn')
      }
    })
  }
  
  window.addEventListener('scroll', check_if_in_view)

  // window.addEventListener('resize', check_if_in_view)
  document.addEventListener('DOMContentLoaded', check_if_in_view)
}

animatedBlock()

function debounce(func, wait, immediate) {
	let timeout
	return function() {
		let context = this, args = arguments
		let later = function() {
			timeout = null
			if (!immediate) func.apply(context, args)
		}
		let callNow = immediate && !timeout
		clearTimeout(timeout)
		timeout = setTimeout(later, wait)
		if (callNow) func.apply(context, args)
	}
}
 
