// import { albumSlider } from "slider-option"

$("input[type='tel'], input[type='number']").on("keypress", function (evt) {
  if (evt.which < +"46" || evt.which > +"57") {
    evt.preventDefault()
  }
})

$.validator.addMethod("emailMethod", function (value, element) {
  let isEmail = this.optional(element) || /^((([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+(\.([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+)*)|((\x22)((((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(([\x01-\x08\x0b\x0c\x0e-\x1f\x7f]|\x21|[\x23-\x5b]|[\x5d-\x7e]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(\\([\x01-\x09\x0b\x0c\x0d-\x7f]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]))))*(((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(\x22)))@((([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.)+(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))$/i.test(value)
  return isEmail
})

$.validator.addMethod("eitherEmailPhone", function(value, element) {
  let isPhone = (this.optional(element) || /^\+|\d+$/.test(value)) && this.getLength($.trim(value), element) <= 15
  let isEmail = this.optional(element) || /^((([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+(\.([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+)*)|((\x22)((((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(([\x01-\x08\x0b\x0c\x0e-\x1f\x7f]|\x21|[\x23-\x5b]|[\x5d-\x7e]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(\\([\x01-\x09\x0b\x0c\x0d-\x7f]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]))))*(((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(\x22)))@((([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.)+(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))$/i.test(value)

  return isPhone || isEmail

}, "Please enter either phone or e-mail")

$.validator.addMethod("alpha", function (value, element) {
  return (
    this.optional(element) || value == value.match(/^[a-zA-Zа-яА-Я0-9-ЯёЁІіЇїЄєҐґ'-_\. ]+$/)
  )
})

$.validator.addMethod("space", function (value, element) {
  return this.optional(element) || value == value.match(/^[\S]+$/)
})

$.validator.addMethod("names", function (value, element) {
  return (
    this.optional(element) || value == value.match(/^[a-zA-Zа-яА-Я-ЯёЁІіЇїЄєҐґ'][a-zA-Zа-яА-Я0-9-_\.' ']{1,35}$/u)
  )
})

$.validator.addMethod("phone", function (value, element) {
  let isPhone = (this.optional(element) || /^\+|\d+$/.test(value)) && this.getLength($.trim(value), element) <= 15
  return isPhone
})

function openPopup(element) {
  $(".wrapper").addClass("is-blur")
  $(element).fadeIn()
  $(element)
    .children()
    .fadeIn()
  if ($(window).innerWidth() < +"992") {
    $("body").css("overflow", "hidden")
  }

  let subject = $(".js-ajax-popup")
  $(".wrapper").mouseup(function (e) {
    if (e.target.id != subject.attr("id") && !subject.has(e.target).length) {
      subject.fadeOut()
      subject.html("")
      if ($(window).innerWidth() < +"992") {
        $("body").css("overflow-y", "scroll")
      }
    }
    $(".wrapper").removeClass("is-blur")
  })

  $(".js-close-popup").on("click", () => {
    subject.fadeOut()
    subject.html("")
    $(".wrapper").removeClass("is-blur")
    if ($(window).innerWidth() < +"992") {
      $("body").css("overflow-y", "scroll")
    }
  })

}

let answerHandler = function (result, link) {
  let id = $(".js-ajax-popup")

  if ("undefined" != typeof result.total) {
    result.total.forEach(element => {
      $(".form__label").each((i, el) => {
        $(el)
          .find("strong")
          .html(element)
      })
    })
  }

  if ("undefined" != typeof result.message) {
    let $popup = id
    $popup.html(result.message)
  }

  // console.log(result)
  // console.log(typeof result)

  if ("string" === typeof result || "object" === typeof result) {
    $(".js-ajax-popup").html(result)
    openPopup(".js-ajax-popup")
    $(document).trigger("dom-bind", $(".js-ajax-popup"))
    // console.log('binded')
  }

  if ("undefined" != typeof result.redirect_url) {
    let redirect = x => (window.location = result.redirect_url)

    if (typeof result.message != undefined) {
      setTimeout(redirect, +"500")
    } else {
      redirect()
    }
  }
}

$("span[data-text]").click(function () {
  const $title = $(this).find(".form__label-title")

  if (!$title.length) {
    let span = $(`<span class="form__label-title">
                    ${$(this).attr("data-text")}
                  </span>`)
    $(this).append(span)
  } else {
    $title.remove()
  }
})

$(document).on("dom-bind", function (e, context) {
  // console.log('binded')
  $(context)
    .find(".js-link-ajax-popup")
    .each(function () {
      let $this = $(this)
      $this.on("click", function (e) {
        e.preventDefault()
        if ($this.attr("data-href")) {
          $.ajax({
            method: "GET",
            url: $this.attr("data-href"),
            
            success: function (result) {
              answerHandler(result)

              // albumSlider()
            },
            
            error: function (result) {}
          })
        }
      })
    })
  $(context)
    .find(".js-form-ajax")
    .each(function () {
      var $this = $(this)
      var rules = JSON.parse($this.attr("data-validation") || "{}")

      // console.log(rules)
      window.validate = $this.validate({
        rules: rules,
        invalidHandler: function (event, validator) {
          // console.log(event, validator)
        },
        submitHandler: function (form) {
          // console.log(true)
          let validator = this
          let $form = $(form)
          let data = new FormData()
          let el = $form.find('*[name]')
          el.each(function (index, value) {
            let type = $(value).attr('type')
            let name = $(value).attr('name')
            let val = $(value).val()

            if ($form.attr('enctype') != undefined) {
              if ('file' == type) {
                data.append(name, $(value).prop('files')[0])
              } else {
                data.append(name, val)
              }
            } else {
              data.append(name, val)
            }
          })

          $.ajax({
            method: $form.attr("method"),
            url: $form.attr("action"),
            processData: false,
            contentType: false,
            data: data,
            success: function (result) {
              // console.log('success')

              // if ($(form).data("redirect")) {
              //   window.location.href = $(form).data("redirect")
              // }
              
              // $form[0].reset()
              $($form)[0].reset()
              answerHandler(result)
              $(".js-form-ajax").trigger("reset")
              $(form)
                .find("[type='submit']")
                .removeAttr("disabled")

            },
            error: function (result) {
              let errors = result.responseJSON.errors
              if (errors.non_field_errors != undefined) {
                $form
                  .find('label[data-nonfielderror="error"]')
                  .html(errors.non_field_errors)
              } else {
                for (field in errors) {
                  errors[field] = errors[field].join(", ")
                }
              }
              validator.showErrors(errors)
              $(form)
                .find("[type='submit']")
                .removeAttr("disabled")
            }
          })
        }
      })
    })
})

$(function () {
  $(document).trigger("dom-bind", document)
})



