window.initMap = initMap

window.addEventListener("load", () => {
  let elements = Array.from(document.getElementsByClassName("js-map"))
  elements.forEach(el => {
    initMap(el)
  })
})

function initMap(el) {
  // let data = el.dataset.title
  let address = document.querySelector('.js-map').dataset.address

  let selector = el
  let data = JSON.parse(el.dataset.coord)
  let center = { lat: data.lat, lng: data.lng }
  let scrollwheel = false
  let offset = window.innerWidth < 1200 ? 0 : 1
  let map = new google.maps.Map(selector, {
    scrollwheel: scrollwheel,
    // center: center,
    // center: {lat: data.lat, lng: data.lng + 0.9},
    center: {lat: data.lat, lng: data.lng + offset},
    zoom: 9,
    styles: [
      {
        featureType: "water",
        elementType: "geometry.fill",
        stylers: [
          {
            color: "#d3d3d3"
          }
        ]
      },
      {
        featureType: "transit",
        stylers: [
          {
            color: "#808080"
          },
          {
            visibility: "off"
          }
        ]
      },
      {
        featureType: "road.highway",
        elementType: "geometry.stroke",
        stylers: [
          {
            visibility: "on"
          },
          {
            color: "#b3b3b3"
          }
        ]
      },
      {
        featureType: "road.highway",
        elementType: "geometry.fill",
        stylers: [
          {
            color: "#ffffff"
          }
        ]
      },
      {
        featureType: "road.local",
        elementType: "geometry.fill",
        stylers: [
          {
            visibility: "on"
          },
          {
            color: "#ffffff"
          },
          {
            weight: 1.8
          }
        ]
      },
      {
        featureType: "road.local",
        elementType: "geometry.stroke",
        stylers: [
          {
            color: "#d7d7d7"
          }
        ]
      },
      {
        featureType: "poi",
        elementType: "geometry.fill",
        stylers: [
          {
            visibility: "on"
          },
          {
            color: "#ebebeb"
          }
        ]
      },
      {
        featureType: "administrative",
        elementType: "geometry",
        stylers: [
          {
            color: "#a7a7a7"
          }
        ]
      },
      {
        featureType: "road.arterial",
        elementType: "geometry.fill",
        stylers: [
          {
            color: "#ffffff"
          }
        ]
      },
      {
        featureType: "road.arterial",
        elementType: "geometry.fill",
        stylers: [
          {
            color: "#ffffff"
          }
        ]
      },
      {
        featureType: "landscape",
        elementType: "geometry.fill",
        stylers: [
          {
            visibility: "on"
          },
          {
            color: "#efefef"
          }
        ]
      },
      {
        featureType: "road",
        elementType: "labels.text.fill",
        stylers: [
          {
            color: "#696969"
          }
        ]
      },
      {
        featureType: "administrative",
        elementType: "labels.text.fill",
        stylers: [
          {
            visibility: "on"
          },
          {
            color: "#737373"
          }
        ]
      },
      {
        featureType: "poi",
        elementType: "labels.icon",
        stylers: [
          {
            visibility: "off"
          }
        ]
      },
      {
        featureType: "poi",
        elementType: "labels",
        stylers: [
          {
            visibility: "off"
          }
        ]
      },
      {
        featureType: "road.arterial",
        elementType: "geometry.stroke",
        stylers: [
          {
            color: "#d6d6d6"
          }
        ]
      },
      {
        featureType: "road",
        elementType: "labels.icon",
        stylers: [
          {
            visibility: "off"
          }
        ]
      },
      {},
      {
        featureType: "poi",
        elementType: "geometry.fill",
        stylers: [
          {
            color: "#dadada"
          }
        ]
      }
    ]
  });
  

  let icon = el.dataset.marker
  // console.log(icon)
  // let icon = '../static/img/marker.png'
  let marker = new google.maps.Marker({
    map: map,
    position: center,
    icon: icon,
  })

}


