let menuToggle = () => {
  let headerBlock = document.querySelector('.header')
  let menuItem = headerBlock.querySelector('.header__menu')
  let mobileBtn = headerBlock.querySelector('.mobile-btn')

  mobileBtn.addEventListener('click', function () {
    menuItem.classList.toggle('is-active')
    mobileBtn.classList.toggle('is-active')
    document.querySelector('body').classList.toggle('is-active')
  })
}
menuToggle()


let searchToggle = () => {
  let searchBlock = [...document.querySelectorAll('.js-search')]
  
  searchBlock.forEach(el => {
 
      let searchBtn = el.querySelector('.icon-search')
      let searchBtnClose = el.querySelector('.icon-close')
      let searchField = el.querySelector('.search__field')
      let searchInput = el.querySelector('.search__input')

      searchBtn.addEventListener('click', function (e) {
        e.preventDefault()
        searchField.classList.add('is-active', 'animated', 'fadeIn')
        searchBtn.classList.add('is-hide')
        searchBtnClose.classList.remove('is-hide')
        searchInput.focus()
      })

      searchBtnClose.addEventListener('click', function (e) {
        searchField.classList.remove('is-active', 'animated', 'fadeIn')
        searchBtnClose.classList.add('is-hide')
        searchBtn.classList.remove('is-hide')
      })
      
      сloseSearchPanel(searchField, searchBtn, searchBtnClose)
  })
}

let сloseSearchPanel = (panel, btnOpen, btnClose) => {
  const nullHeight = 0

  document.addEventListener('mouseup', function (e) {
    if (!$(panel).is(e.target) && nullHeight === $(panel).has(e.target).length && $(panel).hasClass('is-active')) {
      $(panel).removeClass('is-active')
      btnClose.classList.add('is-hide')
      btnOpen.classList.remove('is-hide')
    }
  })
}

searchToggle()


let languagePanel = () => {
  let languageBlock = [...document.querySelectorAll('.language')]
  
  languageBlock.forEach(el => {
    drpoDown(el)
  })

  function drpoDown(el) {
    let langBtn = el.querySelector('.language__btn')
    let contentItem = el.querySelector('.language__content')
    let allLanguages = [...contentItem.children]

    // console.log(allLanguages)

    el.addEventListener('click', function (e) {
      contentItem.classList.toggle('is-active')
    })
    
    el.addEventListener('mouseleave', function (e) {
      contentItem.classList.remove('is-active')
    })


    allLanguages.forEach(el => {
      el.addEventListener('click', function (e) {
        if (e.target) {
          langBtn.innerHTML = e.target.innerHTML.slice(0, 2)
        }
      })
    })
  }
}
languagePanel()


function mobileMenu() {
  let mobileMenu = document.querySelector('.js-mob-menu')
  let mobileMenuItems = [...mobileMenu.children]

  mobileMenuItems.forEach(el => {
    if(el.classList.contains('has-submenu')) {
      let clone = el.cloneNode(false)

      // console.log(el.querySelector('.arrow').innerHTML)

      clone.innerHTML = el.querySelector('.arrow').innerHTML
      // clone.innerHTML = el.firstChild.dataset.menuName
      // clone.innerHTML = el.firstChild.innerHTML
      // clone.innerHTML = 'Назад'
      clone.classList.remove('has-submenu')
      clone.classList.add('back')
      
      openSubmenu(el, clone)
    }
  })

  function openSubmenu(element, clone) {
    element.addEventListener('click', function(e) {

      element.querySelector('.nav--sub').classList.add('is-active')
      element.querySelector('.nav--sub').insertBefore(clone, element.querySelector('.nav--sub').firstChild)
      
      back(element)
    })
  }   

  function back(element) {
    let back = mobileMenu.querySelector('.back')
    back.addEventListener('click', function(e) {
      event.stopPropagation()
      element.querySelector('.nav--sub').classList.remove('is-active')
    })
  }
}

// if(window.innerWidth < 992) {
//   document.querySelector('.nav--sub').classList.remove('is-active')
// }

window.addEventListener('DOMContentLoaded', function() {
  mobileMenu()
})

