let dropdownFooter = () => {
  let dropdownItem = [...document.querySelectorAll('.js-dropdown-footer')]

  dropdownItem.forEach(el => {
    dropdown(el)
  })

  function dropdown(element) {
    element.addEventListener('click', function (e) {

      let list = e.target.nextElementSibling
      
      if(list.classList.contains('is-hide')) {
        closeDropdown()
        list.classList.remove('is-hide')
      } else {
        list.classList.add('is-hide')
      }
    })
  }

  function closeDropdown () {
    dropdownItem.forEach(el => {
      let hide  = el.nextElementSibling
      
      if(hide.classList.contains('is-hide')) {
        return
      } else {
        hide.classList.add('is-hide')
      }
    })
  }
}


dropdownFooter()



