if (!Element.prototype.matches) {
  Element.prototype.matches = Element.prototype.msMatchesSelector || 
                              Element.prototype.webkitMatchesSelector;
}

if (!Element.prototype.closest) {
  Element.prototype.closest = function(s) {
    var el = this;

    do {
      if (el.matches(s)) return el;
      el = el.parentElement || el.parentNode;
    } while (el !== null && el.nodeType === 1);
    return null;
  };
}

if (window.Element && !Element.prototype.closest) {
  Element.prototype.closest =
  function(s) {
    var matches = (this.document || this.ownerDocument).querySelectorAll(s),
        i,
        el = this;
    do {
      i = matches.length;
      while (--i >= 0 && matches.item(i) !== el) {};
    } while ((i < 0) && (el = el.parentElement));
    return el;
  };
}

function tabs() {
  let bindAll = function () {
    let menuElements = [...document.querySelectorAll('[data-point-number]')]
    menuElements.forEach(el => {
      el.addEventListener('click', change, false)
    })
  }

  let clear = function () {
    let menuElements = [...document.querySelectorAll('[data-point-number]')]
    menuElements.forEach(el => {
      el.classList.remove('is-active')
      let id = el.getAttribute('data-point-number')
      document.querySelector(`[data-info-number='${id}']`).classList.remove('is-active', 'animated', 'fadeIn')
    })
  }

  let change = function (e) {
    dotsColored(e.target)
    line(e.target)
    clear()
    e.target.closest('.timeline__elem', 'is-active').classList.add('is-active')
    let id = e.currentTarget.getAttribute('data-point-number')
    document.querySelector(`[data-info-number='${id}']`).classList.add('is-active', 'animated', 'fadeIn')
  }

  let dotsColored = function (elem) {
    let test = elem.closest('.timeline__elem', 'is-active')
    let test2 = [...document.querySelectorAll('.timeline__elem')]
    test2.forEach(el => {
      el.classList.remove('previous')
    })
    $(test).prevAll().addClass('previous')
  }

  let line = function (elem) {
    let timeLine = document.querySelector('.timeline__color-line')
    let targetElem = elem.closest('.timeline__elem', '.is-active')
    // let type = window.innerWidth < 768 ? 'height' : 'width'
    // let elementType = window.innerWidth < 768 ? 'offsetTop' : 'left'
    
    // let elementType = window.innerWidth < 768 ? 'offsetTop' : 'offsetLeft'
    // let size = window.innerWidth < 768 ? targetElem[elementType] + (targetElem.getBoundingClientRect()[type] + 5) : targetElem.getBoundingClientRect()[elementType] + (targetElem.getBoundingClientRect()[type] / 2)
    // let size = targetElem[elementType] + (targetElem.getBoundingClientRect()[type] / 2)
    // console.dir(targetElem)

    let size = targetElem.getBoundingClientRect().left + (targetElem.getBoundingClientRect().width / 2)
    timeLine.style.width = size + 'px'
    
    // console.log(targetElem[elementType])
    // console.log(timeLine)
    // console.log(width)
  }

  dotsColored(document.querySelector('.timeline__elem.is-active'))
  line(document.querySelector('.timeline__elem.is-active'))
  bindAll()
}

if(document.querySelector('.timeline')) {
  tabs()
  
  window.addEventListener('resize', function() {
    tabs()
  })
  
  window.onload = function() {
    let elem = document.querySelector(".js-scroll"),
        scroll = 0
  
    elem.onscroll = function() {
      if (this.scrollLeft != scroll) {
  
        tabs()
  
      }
    }

    // const simpleBar = new SimpleBar(elem, {
    //   autoHide: false,
      
    // })

    // simpleBar.getScrollElement().addEventListener('scroll', function() {
    //   // tabs()
    //   console.log(1)
    // })

  }
}

