import { albumSlider } from "slider-option"

let openAlbumPopup = () => {

  let imageLinks = [...document.querySelectorAll('.js-link-album-popup')]
  let popup = document.querySelector('.album-popup')
  let close = document.querySelector('.js-close-popup')


  imageLinks.forEach((el, index) => {
    el.addEventListener('click', function() {
      // document.querySelector('body').style.overflow = 'hidden'
      // document.querySelector('body').style.paddingRight = '15px'
      popup.style.overflow = 'visible'
      $(popup).fadeIn()
      // albumSlider()

      let albumSliderThumbs = new Swiper('.album-slider-thumbs', {
        spaceBetween: 30,
        slidesPerView: 6,
        watchSlidesVisibility: true,
        watchSlidesProgress: true,
        breakpoints: {
          1200: {
            slidesPerView: 4
          },
          768: {
            slidesPerView: 3
          },
          576: {
            slidesPerView: 2
          }
        }
      })
      
      let albumSliderTop = new Swiper('.album-slider-top', {
        navigation: {
          nextEl: '.swiper-album-next',
          prevEl: '.swiper-album-prev',
        },
        thumbs: {
          swiper: albumSliderThumbs
        }
      })

      albumSliderTop.slideTo(index, 1000, false)

    })
  })

  close.addEventListener('click', function() {
    document.querySelector('body').style.overflow = ''
    // document.querySelector('body').style.paddingRight = ''
    $(popup).fadeOut()
  })

}

if (document.querySelector('.album') || document.querySelector('.gallery-image')) {
  openAlbumPopup()
}



